
import HeaderBar from '../../components/header.vue'
import FooterBar from '../../components/footer.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'


import {
    mapState, mapActions
}
from 'vuex'
export default {
    components: {
        HeaderBar,
        FooterBar,
        Grid,
        GridItem,
        GroupTitle
    },
    computed: {
        ...mapState([
            'isUserDetails'
        ])
    },
    methods: {
        processButton(url) {
          if(this.isUserDetails)
          {
            this.$vux.alert.show({
              title: '',
              content: ''
            })
            return
          }
          if(url){
            this.$router.push({
              'path': url
            })
          }
          return false;
        }
    },
    data() {
        return {
            categorys: [{
                text: '',
                url: '/rooms',
                class_name: 'fa fa-id-badge fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-address-card-o fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-envelope-open-o fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-university fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-handshake-o fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-wrench fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-id-badge fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-usd fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-hand-spock-o fa-2x'
            }, {
                text: '',
                url: '',
                class_name: 'fa fa-hand-peace-o fa-2x'
            }]
        }
    }
}




